/* Movie.css */

.body {
    font-family: 'Netflix Sans', Arial, sans-serif;
    background-color: #141414;
    color: #fff;
    margin: 0;
    padding: 0;
    overflow-x: hidden; 
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: rgba(20, 20, 20, 0.7);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
  }
  
  .header .logo {
    font-size: 2rem;
    font-weight: bold;
    color: #d731b9;
  }
  
  .header .category-container, .icons-container {
    display: flex;
  }
  
  .header .category, .icon-link {
    margin-right: 20px;
    color: #fff;
    text-decoration: none; 
    cursor: pointer;
  }
  
  .vod-container.h2 {
    display: grid;
    grid-template-columns: repeat(5, 1fr); 
    grid-gap: 20px; 
    padding: 20px;
    margin-top: 100px; 
  }
  
  .movie-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .movie-item img {
    width: 100%; 
    height: auto; 
    border-radius: 8px;
  }
  
  .movie-item h3 {
    color: white;
    font-size: 14px;
    margin-top: 5px; 
  }
  